<template>
  <div class="swan-car">
    <box>
      <div class="search">
        <div class="search-body">
          <a-form-model
            class="default-search-form"
            layout="inline"
            :model="form"
            @submit="handleSubmit"
          >
            <a-form-model-item label="位置：">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                placeholder="请选择"
                v-model="form.address"
                :get-popup-container="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="">
                  请选择
                </a-select-option>
                <a-select-option value="1">
                  左侧车道
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="类别：">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                placeholder="请选择"
                v-model="form.gslb"
                :get-popup-container="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="车道">
                  车道
                </a-select-option>
                <a-select-option value="卡口">
                  卡口
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="号牌种类：">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                placeholder="请选择"
                v-model="form.hpzl"
                :get-popup-container="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="">
                  请选择
                </a-select-option>
                <a-select-option value="1">
                  蓝色
                </a-select-option>
                <a-select-option value="2">
                  绿色
                </a-select-option>
                <a-select-option value="3">
                  黄色
                </a-select-option>
                <a-select-option value="4">
                  黄绿色
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="车牌号码：">
              <a-input
                v-model="form.hphm"
                class="default-input"
                placeholder="请输入"
              >
              </a-input>
            </a-form-model-item>
            <a-form-model-item label="开始时间：">
              <a-date-picker
                separator=""
                show-time
                v-model="form.kssj"
                :disabled-date="disabledStartDate"
                dropdown-class-name="default-date-picker-dropdown default-date-picker-dropdown-top"
                class="default-date-picker"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
            <a-form-model-item label="结束时间：">
              <a-date-picker
                separator=""
                show-time
                v-model="form.jssj"
                :disabled-date="disabledEndDate"
                dropdown-class-name="default-date-picker-dropdown default-date-picker-dropdown-top"
                class="default-date-picker"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
          </a-form-model>
        </div>
        <div class="operation-btn">
          <div class="operation-left">
            <a-button
              class="operation-button"
              type="primary"
              html-type="submit"
            >
              <a-icon type="search" /> 搜索
            </a-button>
          </div>
          <div class="operation-right">
            <div
              class="search-type-item"
              :class="{ 'search-type-item-sel': !isTable }"
              @click="setIsTable(false)"
            >
              栅
            </div>
            <div
              class="search-type-item"
              :class="{ 'search-type-item-sel': isTable }"
              @click="setIsTable(true)"
            >
              表
            </div>
          </div>
        </div>
      </div>
      <div class="content-body">
        <cards
          v-show="!isTable"
          :vehicleData="lists"
          @openModal="openModal"
        ></cards>
        <tables
          v-show="isTable"
          :vehicleData="lists"
          @openModal="openModal"
        ></tables>
      </div>
      <div class="content-pagination">
        <pagination
          ref="pagination"
          :total="500"
          @change="onPaginationChange"
        />
      </div>
    </box>
    <mdetails ref="details" :visible="visible" @close="closeModal"></mdetails>
  </div>
</template>

<script>
import Box from "../../components/Box";
import Pagination from "../../components/Pagination";
import moment from "moment";
import cards from "./cards";
import mdetails from "./details";
import tables from "./tables";

import { dateCompare } from "../../utils/util";

export default {
  components: {
    Box, // 大边框
    Pagination, // 分页器
    cards, // 卡片模式
    mdetails, // 弹框详情
    tables // 表格模式
  },
  data() {
    return {
      lists: [],
      isTable: false,
      visible: false,
      pageSize: 10,
      page: 1,
      // 车道 卡口
      form: {
        zbgsmc: "",
        gslb: "",
        zbmc: "",
        hpzl: "",
        hphm: "",
        kssj: moment("2021-01-01 00:00:00", "YYYY-MM-DD HH:mm:ss"),
        jssj: moment("2021-01-02 00:00:00", "YYYY-MM-DD HH:mm:ss"),
        page: 0,
        size: 10
      }
    };
  },
  mounted() {
    this.$refs.pagination.setData(this.form.page + 1, this.form.size);
    this.getVehicleInfo();
  },

  methods: {
    moment,

    /**
     * @param {*} startValue
     * @return {*}
     */
    disabledStartDate(startValue) {
      const endValue = this.form.jssj;
      return dateCompare(startValue, endValue);
    },

    /**
     * s
     * @param {*} endValue
     * @return {*}
     */
    disabledEndDate(endValue) {
      const startValue = this.form.kssj;
      return dateCompare(startValue, endValue);
    },
    /**
     * 分页时间
     */
    onPaginationChange(page, pageSize) {
      this.form.page = page - 1;
      this.form.size = pageSize;
      this.getVehicleInfo();
    },

    getVehicleInfo() {
    //   this.$api.getVehicleInfo(this.form).then(data => {
    //     console.log(data);
    //     if (data.data.list) {
    //     }
    //   });
        let lists = [];
        for (let i = 0; i < this.pageSize; i++) {
          let item = {
            no: i + 1, // 序号
            card: "皖A·03EU8", // 车牌号
            img: "../../assets/img/charts/img-car.png", // 车辆抓拍图
            time: "2021.04.15 15:37:50", // 通行时间
            address: "岗集入城卡口相机一", // 位置
            type: i % 3, // 车牌种类，0 蓝色，1 绿色，2 黄色
            carType: "小型车", // 号牌种类
            cardColor: "蓝色", // 车牌颜色
            carColor: "银色" // 车身颜色
          };
          lists.push(item);
        }
        this.lists = lists;
    },

    /**
     * 打开弹框
     */
    openModal() {
      this.visible = true;
      this.$refs.details.initTabs(Number(this.form.type));
    },

    /**
     * 关闭弹框
     */
    closeModal() {
      this.visible = false;
    },

    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log("Received values of form: ", values);
        }
      });
    },

    setIsTable(isTable) {
      this.isTable = isTable;
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
