-->
<template>
  <div class="swan-car-cards">
    <div class="row">
      <div v-for="(item, index) in vehicleData" :key="index" class="col">
        <triangle-box>
          <!-- @click="$emit('openModal')" -->
          <div class="vehicle-car" @click="$emit('openModal')">
            <div class="vehicle-top">
              <img src="../../assets/img/charts/img-address-icon.png" />
              <span class="text-ellipsis">
                {{ item.address }}
              </span>
            </div>
            <div class="vehicle-middle">
              <img src="../../assets/img/charts/img-car.png" />
              <div
                class="license-plate"
                :class="{
                  'licence-plate-blue': item.type == 0,
                  'licence-plate-green': item.type == 1,
                  'licence-plate-yellow': item.type == 2
                }"
              >
                {{ item.card }}
              </div>

              <div class="img-zoom">
                <div class="img-zoom-icon" @click="amplification($event, true)"></div>
              </div>
            </div>
            <div class="vehicle-bottom">
              <img src="../../assets/img/charts/img-time-icon.png" />
              <span class="text-ellipsis">
                {{ item.time }}
              </span>
            </div>
          </div>
        </triangle-box>
      </div>
      <a-modal class="amplification-img-modal" :visible="visible" centered>
        <img src="../../assets/img/charts/img-car.png" />
        <div class="img-zoom-up">
          <div class="img-zoom-up-icon" @click="amplification($event,false)"></div>
        </div>
      </a-modal>
    </div>
  </div>
</template>

<script>
import TriangleBox from "../../components/TriangleBox";

export default {
  components: {
    TriangleBox
  },
  props: {
    /**
     * 车辆数组
     */
    vehicleData: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      isOpen: -1,
      visible: false,
      location: {
        left: 0,
        top: 0
      }
    };
  },
  methods: {
    amplification(e, visible) {
      e = e || window.event;
      if (e.stopPropagation) {
        //W3C阻止冒泡方法
        e.stopPropagation();
      } else {
        e.cancelBubble = true; //IE阻止冒泡方法
      }
      this.visible = visible;
    }
  }
};
</script>

<style lang="less" scoped></style>
