<template>
  <div class="swan-car-details">
    <modal
      title="车辆通行档案"
      :visible="visible"
      @close="$emit('close')"
      width="46.51vw"
      height="38.385vw"
    >
      <div class="modal-bg" slot="modalBg"></div>
      <div class="details">
        <a-tabs
          class="record-tabs"
          :class="`record-tabs-${activeKey}`"
          v-model="activeKey"
          @change="callback"
        >
          <a-tab-pane key="1" :tab="carType == 1 ? '车道安检' : '卡口通行'">
            <details-lane-security></details-lane-security>
          </a-tab-pane>
          <a-tab-pane key="2" :tab="carType == 1 ? '卡口通行' : '车道安检'">
            <details-mount-passage></details-mount-passage>
          </a-tab-pane>
          <a-tab-pane key="3" tab="预警处置">
            <details-warning></details-warning>
          </a-tab-pane>
          <a-tab-pane key="4" tab="物品处置">
            <details-goods></details-goods>
          </a-tab-pane>
        </a-tabs>
      </div>
    </modal>
  </div>
</template>

<script>
import TriangleBox from "../../components/TriangleBox";
import Modal from "../../components/Modal";
import DetailsLaneSecurity from "./detailsLaneSecurity";
import DetailsMountPassage from "./detailsMountPassage";
import DetailsWarning from "./detailsWarning";
import DetailsGoods from "./detailsGoods";

import moment from "moment";

export default {
  components: {
    TriangleBox,
    Modal,
    DetailsLaneSecurity,
    DetailsMountPassage,
    DetailsWarning,
    DetailsGoods
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeKey: "1",
      carType: 1
    };
  },
  methods: {
    moment,

    /**
     * 初始化弹框中tabs
     * @param {*} carType 车道类型
     * @return {*}
     */
    initTabs(carType) {
      this.activeKey = "1";
      this.carType = carType;
    },

    callback() {}
  }
};
</script>

<style lang="less" scoped></style>
