/**
 * 时间比较
 * @param {*} startValue 开始时间
 * @param {*} endValue 结束时间
 */
export const dateCompare = (startValue, endValue) => {
console.log(startValue, endValue)
  if (!startValue || !endValue) {
    return false;
  }
  return startValue.valueOf() >= endValue.valueOf();
};

