<template>
  <div class="swan-car-table">
    <a-table
      class="default-table"
      :pagination="false"
      :columns="columns"
      :data-source="vehicleData"
      rowKey="no"
      :scroll="{ y: 'calc(90vh - 20.5vw)' }"
    >
      <!-- 表格中车牌号插槽 start -->
      <span slot="card" slot-scope="text, record" @click="click(text, record)">
        <div class="blue" v-if="record.type == 0">
          {{ text }}
        </div>
        <div class="green" v-else-if="record.type == 1">
          {{ text }}
        </div>
        <div class="yellow" v-else-if="record.type == 2">
          {{ text }}
        </div>
      </span>
      <!-- 表格中车牌号插槽 end -->

      <!-- 表格中车辆抓拍图插槽 start -->
      <span slot="img" @click="amplification($event, true)">
        <img class="card-img" src="../../assets/img/charts/img-car.png" />
      </span>
      <!-- 表格中车辆抓拍图插槽 end -->
    </a-table>
    <a-modal class="amplification-img-modal" :visible="visible" centered>
      <img src="../../assets/img/charts/img-car.png" />
      <div class="img-zoom-up">
        <div
          class="img-zoom-up-icon"
          @click="amplification($event, false)"
        ></div>
      </div>
    </a-modal>
  </div>
</template>

<script>
/**
 * 表头
 */
const columns = [
  {
    title: "序列",
    dataIndex: "no",
    key: "no"
  },
  {
    title: "车牌号",
    key: "card",
    dataIndex: "card",
    scopedSlots: { customRender: "card" }
  },
  {
    title: "车辆抓拍图",
    dataIndex: "img",
    key: "img",
    scopedSlots: { customRender: "img" }
  },
  {
    title: "号牌种类",
    dataIndex: "carType",
    key: "carType"
  },
  {
    title: "车牌颜色",
    dataIndex: "cardColor",
    key: "cardColor"
  },
  {
    title: "车身颜色",
    dataIndex: "carColor",
    key: "carColor"
  },
  {
    title: "通行时间",
    dataIndex: "time",
    key: "time",
    width: "14vw",
    ellipsis: true
  },
  {
    title: "位置",
    dataIndex: "address",
    key: "address",
    width: "13vw",
    ellipsis: true
  }
];

export default {
  props: {
    /**
     * 车辆数组
     */
    vehicleData: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      columns,
      visible: false,
    };
  },
  methods: {
    /**
     * 表格点击事件
     */
    click(text, record) {
      console.log("-----");
      this.$emit("openModal");
    },

    amplification(e, visible) {
      e = e || window.event;
      if (e.stopPropagation) {
        //W3C阻止冒泡方法
        e.stopPropagation();
      } else {
        e.cancelBubble = true; //IE阻止冒泡方法
      }
      this.visible = visible;
    }
  }
};
</script>

<style lang="less" scoped></style>
